@keyframes fadeInDown {
	0% {
		opacity: 0;
		transform: translateY(-500px);
	}
	100% {
		opacity: 1;
		transform: translateY(0);
	}
}

@keyframes fadeInUp {
	0% {
		opacity: 0;
		transform: translateY(500px);
	}
	100% {
		opacity: 1;
		transform: translateY(0);
	}
}

@keyframes fromRight {
	0% {
		transform: translateX(500px);
	}
	100% {
		transform: translateY(0);
	}
}
